.loader {
    position: absolute;
    left: 50%;
    top: 35%;
    border: 4px solid #d1d1d1;
    border-radius: 50%;
    border-top: 4px solid #313131;
    width: 40px;
    height: 40px;
    animation: load 1.5s linear infinite;
}

@keyframes load {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
