.itemPage__details {
    flex-basis: 100%;
    display: flex;
    flex-direction: column;

    .details__caption {
        margin-top: 0.3em;

        h2,
        h3 {
            font-weight: normal;
        }

        * {
            padding: 0.3em;
        }
    }

    select {
        margin: 16px 0.5em;
        padding: 0.4em;
    }

    button {
        margin: 0.5em;
        padding: 0.4em;
    }
}

@media (min-width: 600px) {
    .itemPage__details {
        flex-basis: 50%;
    }
}
