@import '../../../base';

.navbar {
    display: block;

    .navbar__header {
        flex-basis: 100%;
        display: flex;
        align-items: center;
        position: relative;
        margin: 0 10px;
        justify-content: flex-end;

        h2 {
            flex-grow: 1;
            text-align: left;
            font-weight: normal;
        }

        a {
            padding: 10px;
        }

        .nav--hamburger {
            cursor: pointer;
            padding: 10px;

            * {
                cursor: pointer;
            }
        }
    }

    .navbar__nav {
        display: none;
    }

    .navbar--hamburger {
        display: block;
        text-align: center;

        .nav__link {
            padding: 10px;
        }
    }

    a {
        display: block;
        color: black;
    }
}

@media (min-width: 800px) {
    .navbar {
        display: flex;
        flex-wrap: wrap;
        position: relative;

        .navbar__header {
            flex-basis: 100%;
            display: flex;
            position: relative;
            justify-content: flex-end;

            h2 {
                padding: 10px 0;
                position: absolute;
                left: 50%;
            }

            .nav--hamburger {
                display: none;
            }
        }

        .navbar__nav {
            display: flex;
            justify-self: start;
            flex-basis: 100%;
        }

        .navbar__rest {
            display: flex;
            justify-self: end;
        }

        .nav__link {
            height: 100%;
            margin: 0 10px;
            padding: 10px;
            color: black;
        }
    }
}
